:root {
  --main-font: "Montserrat", sans-serif;
  --secondary-font: "Seymour One", sans-serif;
  --main-txt-cl: #fcfcfc;
  --accent-txt-cl: #131313;
  --bright-txt-cl: #f8d24e;
  --address-txt-color: #d6936d;
  --light-txt-color: #ababab;
  --accent-bg-cl: #84a178;
  --btn-bg-color: #f8d24e;
}

body {
  font-family: var(--main-font);
  color: var(--main-txt-cl);
  font-size: 16px;
  line-height: 1.75;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.btn {
  width: 114px;
  height: 42px;
  background: var(--btn-bg-color);
  color: var(--accent-txt-cl);
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.25;
  display: flex;
}

